
.myfoot{
	width:100%;
	height:4.8rem;
	background:pink;
	position:fixed;
	bottom:0;
}
.myfoot ul{
	width:100%;
	height:100%;
	display:-webkit-box;
	display:-ms-flexbox;
	display:flex;
	-ms-flex-pack: distribute;
	    justify-content: space-around;
	-webkit-box-align: center;
	    -ms-flex-align: center;
	        align-items: center;
	text-align: center;
}
.myfoot ul a{
	width:25%;
	color:black;
	text-decoration: none;
	font-size:1.6rem;
}
.myfoot ul a.router-link-active{
	color:red;
}



